/* src/styles/ProductCard.css */

/* --- Grid Section --- */
.featured-section {
  width: var(--container-width);
  margin: 60px auto;
  padding: 0 20px;
}

.section-title {
  font-family: 'Georgia', serif;
  font-size: 32px;
  color: var(--primary-green);
  text-align: center;
  margin-bottom: 40px;
}

.product-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr); /* 4 cards per row */
  gap: 30px;
}

/* --- The Card --- */
.product-card {
  background: white;
  border: 1px solid #eee;
  border-radius: 8px;
  overflow: hidden;
  transition: transform 0.2s, box-shadow 0.2s;
  display: flex;
  flex-direction: column;
}

.product-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0,0,0,0.08);
}

.card-image-container {
  position: relative;
  height: 200px;
  overflow: hidden;
}

.card-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.new-badge {
  position: absolute;
  top: 10px;
  left: 10px;
  background-color: var(--accent-gold);
  color: var(--text-dark);
  padding: 4px 10px;
  font-size: 12px;
  font-weight: bold;
  border-radius: 4px;
}

.card-details {
  padding: 15px;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.card-title {
  font-size: 18px;
  color: var(--primary-green);
  margin: 0 0 10px 0;
  font-weight: bold;
  height: 44px; /* Limits title to 2 lines */
  overflow: hidden;
}

.card-rating {
  color: var(--accent-gold);
  margin-bottom: 10px;
  font-size: 14px;
}

.card-footer {
  margin-top: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 15px;
  border-top: 1px solid #eee;
}

.price {
  font-size: 18px;
  font-weight: bold;
  color: var(--text-dark);
}

.add-btn {
  background-color: var(--primary-green);
  color: white;
  border: none;
  padding: 8px 15px;
  border-radius: 20px;
  font-weight: bold;
  cursor: pointer;
  transition: background 0.2s;
}

.add-btn:hover {
  background-color: var(--dark-green);
}